import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-data',
  templateUrl: './data.component.html',
  styleUrls: ['./data.component.css']
})
export class DataComponent implements OnInit {
  myArray : Array<any>;
  isUserProfile : Boolean = false;
  isGender : String;
  isIndex : Number;

  constructor() {
    this.myArray = [
      {name : "VinothC", sex : "Male", avator : "male.jpg"},
      {name : "Ammu", sex : "Female", avator : "female.jpg"}
    ];
  }
  isMyProfile(index) {
    this.isIndex = index;
    this.isUserProfile = true;
    this.isGender = this.myArray[index].sex;
  }

  ngOnInit() {
  }

}



